import React from "react";
import styled from "styled-components";

const FlexDiv = styled.div`
    display: flex;
    position: relative;
`;

const Wrapper = styled(FlexDiv)`
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
`;

const Container = styled(FlexDiv)`
    flex-direction: column;
    width: 60%;
`;


const Intro = styled(FlexDiv)`
    align-items: center;
    margin-top: 50px;
    flex-direction: column;
    align-items: center;
`;

const Logo = styled.img.attrs({
    src: require("../../assets/checkloud-logo.png")
})`
    width: 80px;
    margin: 40px;
`;
const IntroContainer = styled(FlexDiv)`
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 700px;
`;
const AppName = styled.div`
    font-family: 'Prompt', sans-serif;
    font-size: 18px;
    font-weight: 800;
    color: #333;
    margin-bottom: 10px;

    @media only screen and (max-width: 1050px) {
        align-self: center;
    }
`;
const Summary = styled.span`
    font-family: 'Prompt', sans-serif;
    font-size: 15px;
    font-weight: 600;
    color: #777;
    line-height: 23px;
    margin-bottom: 10px;
    text-align: center;

    @media only screen and (max-width: 1050px) {
        font-size: 15px;
    }

    @media only screen and (max-width: 512px) {
        font-size: 12px;
    }
`;

const LinkContainer = styled.div`
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
`;

const Link = styled.a.attrs({
    target: "_blank"
})`
    margin: 5px;
    height: 25px;
    display: flex;
    text-decoration: none;
    align-items: center;
    justify-content: center;
    
    @media only screen and (min-width: 1050px) {
        width: 230px;
    }
`;
const Git = styled.div`
    flex: 1;
    display: flex;
    font-family: 'Prompt', sans-serif;
    font-size: 17px;
    font-weight: 700;
    color: #349eeb;
    align-items: center;
    justify-content: center;
    
    @media only screen and (max-width: 1050px) {
        font-size: 13px;
    }
`;

export default function() {
    return (
        <Wrapper>
            <Container>
                <Intro>
                    <Logo />

                    <IntroContainer>
                        <AppName>Checkloud</AppName>
                        <Summary>
                            Checkloud        .
                        </Summary>
                        <Summary>
                                           
                                   .
                        </Summary>
                        <Summary>
                                      
                                 .
                        </Summary>
                    </IntroContainer>
                </Intro>

                <LinkContainer>
                    <Link href="https://github.com/jngcii/checkloud-api">
                        <Git>API-Server Github Link</Git>
                    </Link>

                    <Link href="https://github.com/jngcii/checkloud">
                        <Git>Client Github Link</Git>
                    </Link>
                </LinkContainer>
            </Container>
        </Wrapper>
    );
}